export var GetImages = StoreAccessor(function (s) {
  return function () {
    return GetDocs({}, function (a) {
      return a.images;
    });
  };
});
export var GetImagesByURL = StoreAccessor(function (s) {
  return function (url) {
    return GetDocs({
      queryOps: [new WhereOp("url", "==", url)]
    }, function (a) {
      return a.images;
    });
  };
});